import { v1, v4 } from 'uuid';
import md5 from './../node_modules/uuid/dist/esm-node/md5';

export class CryptoUtils {
	public static md5(bytes: any) {
		return md5(bytes);
	}
	public static uuid(version: string) {
		if (version === 'v1') return v1();
		return v4();
	}
}
